/* eslint-disable react/destructuring-assignment */
/* eslint-disable no-unused-expressions */

import React, { useState } from 'react';
import { Checkbox } from 'antd';
import classes from './App.module.css';

const AviaSales = props => {
  const [step, setStep] = useState({
    step: 1,
  });

  let { ticketsList } = props;
  const checkbox = [
    { key: 1, checked: true, id: 1, value: 'Без остановок', steps: 0 },
    { key: 2, checked: true, id: 2, value: '1 остановка', steps: 1 },
    { key: 3, checked: true, id: 3, value: '2 остановки', steps: 2 },
    { key: 4, checked: true, id: 4, value: '3 остановки', steps: 3 },
    { key: 5, checked: true, id: 5, value: 'Все', steps: 5 },
  ];

  const changing = (n, check) => {
    ticketsList = props.mainTickets;
    checkbox[n - 1].id = -1;
    console.log(checkbox[n - 1].id);
    if (check) {
      setStep({
        ...step,
        step: checkbox[n - 1].steps,
      });
    }
  };
  console.log(checkbox[0].id);

  const number = step.step;

  const arr = [];
  for (let i = 0; i < ticketsList.length; i++) {
    const obj = {
      id: i,
      price: ticketsList[i].price,
      company: `//pics.avs.io/99/36/{${ticketsList[i].carrier}}.png`,
      forward: `${ticketsList[i].segments[0].origin}-${ticketsList[i].segments[0].destination}`,
      toward: `${ticketsList[i].segments[1].origin}-${ticketsList[i].segments[0].destination}`,
      timeForward: ticketsList[i].segments[0].duration,
      timeToward: ticketsList[i].segments[1].duration,
      stepsForward: ticketsList[i].segments[0].stops.length,
      stepsToward: ticketsList[i].segments[1].stops.length,
      stepsForwardCities: ticketsList[i].segments[0].stops.join('-'),
      stepsTowardCities: ticketsList[i].segments[1].stops.join('-'),
    };
    arr.push(obj);
  }

  const filter = arr.filter(e => e.stepsForward + e.stepsToward === number);
  const renderingData = [];
  for (let i = 0; i < 5; i++) {
    renderingData.push(filter[i]);
  }
  const mappedTickets = renderingData.filter(e => e !== undefined);

  if (arr.length !== 0) {
    return (
      <div className={classes.body}>
        <ul className={classes.ul}>
          {checkbox.map(el => (
            <li key={el.key}>
              <Checkbox
                onChange={() => changing(el.id, el.checked)}
                checked={el.id === -1 ? el.checked : !el.checked}
              >
                {el.value}
              </Checkbox>
            </li>
          ))}
        </ul>
        <div className={classes.wrapper}>
          {mappedTickets.map(el => (
            <div key={el.id} className={classes.ticketsArea}>
              <ul>
                <li>{el.price}</li>
                <li>{el.toward}</li>
              </ul>
              <ul>
                <li>{el.timeForward}</li>
                <li>{el.timeToward}</li>
              </ul>
              <ul>
                <li>{el.stepsForward}</li>
                <li>{el.stepsToward}</li>
              </ul>
              <ul>
                <li>{el.stepsForwardCities}</li>
                <li>{el.stepsTowardCities}</li>
                <li>
                  <img src={el.company} alt="" />
                </li>
              </ul>
            </div>
          ))}
        </div>
      </div>
    );
  }
  return 'please wait';
};
export default AviaSales;
